@media screen and (max-width: 960px) {
    .rush-container {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 80%;
    }
    .rush-pic {
        text-align: center;
        width: 100%;
        border-radius: 40px;
    }

    .rush-text {
        width: 100%;
    }

    .rush-text > p {
        text-align: center;
    }

